@import '../../../styles/theme';


.styles_modal__gNwvD {
    padding: 0;
}
.confirmation-modal {
    min-width: 300px;
    max-width: 500px;
    .modal-header, .modal-body,.modal-footer {
        padding: 15px;
        border: 0px;
    }
    .modal-header {
        background-color: var(--color-grey);
    }

    .modal-header {
        font-size: var(--font-size-12);
        font-weight: 600;
        text-transform: uppercase;
    }
    .modal-body {
        padding: 50px 15px;
        text-align: center;
        font-size: var(--font-size-14);
    }
    .modal-footer {
        border-top: 1px solid var(--color-grey); 
    }
}